.navbar-container {
    padding-top: 10px;
    position: fixed;
    width: 100%;
    height: 20px; 
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    color: white;
    font-size: 20px;
  }
  
  .nav-item-hover {
    background-color: white;
    height: 100%;
    width: 100%;
    opacity: 0.8;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .nav-item-hover a {
    color: #ffffff;
    display: block; 
    height: 100%; 
  }
  
  .nav-item-hover:hover a {
    color: #000000;
  }

  .hamburger-icon {
    z-index: 10;
    padding-top: 16px;
    padding-bottom: 16px;
    padding-right: 2%;
  }  

  .mobile-menu {
    display: none;
  }

  .desktop-menu {
    display: flex; 
  }

  @media (max-width: 720px) {
    .mobile-menu {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background-color: black;
    }
  
    .mobile-menu li {
      font-size: 30px;
      margin-bottom: 30px; /* Adjust the value to increase or decrease the spacing */
    }
  
    .desktop-menu {
      display: none;
    }
  
    .nav-item-hover {
      height: auto;
      width: auto;
      opacity: 1;
    }
  
    .nav-item-hover a {
      height: auto;
      width: auto;
    }
  }
  
  
